.work_project {
  font-size: 0.95rem;
  padding: 0 0 0.5rem 0;
}

.work_project_description__grid {
  list-style-type: square;
  list-style-position: inside;
}

.work_project__name {
}

.work_project__detail {
  text-align: justify;
  font-size: 0.95rem;
}

.work_project__technologies {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1.2em 0;
}

.work_project__technologies-item {
  margin: 0.5em;
  font-weight: 500;
  font-size: 0.8rem;
  color: var(--clr-fg-alt);
}
